        function checkSimbol(obj, key) {
            var IllegalString = "[`~!#@$^&*()=|{}':;',\\[\\].<>/?~#&*|{}']'";
            var index = obj[key].length - 1;
            var s = obj[key].charAt(index);
            if (IllegalString.indexOf(s) >= 0) {
                s = obj[key].substring(0, index);
                obj[key] = s;
            }
        }

