(function () {
    'use strict';

    angular
        .module('vk')
        .controller('myRechargeController', myRechargeController);

    /** @ngInject */
    myRechargeController.$inject = ['$location', '$timeout', 'UtilService', 'MyService', '$sessionStorage', "lodash", "SessionStorageService"];

    function myRechargeController($location, $timeout, UtilService, MyService, $sessionStorage, lodash, SessionStorageService) {
        var cardId = $sessionStorage.cardId;
        var token = $sessionStorage.token;
        var storeId = $sessionStorage.storeId;
        var openId = $sessionStorage.weChatOpenId;
        var times = 0;

        var vm = this;
        vm.showPaySuccess = false;
        vm.memberName = $sessionStorage.memberName;
        vm.mobile = $sessionStorage.mobile;
        vm.balance = $sessionStorage.balance;
        vm.points = $sessionStorage.point;
        vm.rechargeAmountDisplay = 0;
        vm.onCheck = onCheck;
        vm.recharge = recharge;
        vm.goBack = goBack;

        function goBack() {
            $location.url('/my?storeId=' + storeId);
        }

        function onCheck(item) {
            vm.rechargeAmountDisplay = item.amount;
        }


        function refreshUserInfo() {
            times += 1;
            if (times >= 5) {
                return
            }
            $timeout(function () {
                if(cardId =="" ||cardId=="null"||!cardId){
                    return
                }
                MyService.getMemberInfo(cardId, token).then(function (data) {
                    if (data.code != 200) {
                        alert(data.message);
                        console.log(data.message);
                        return
                    }
                    if ($sessionStorage.balance != data.data.balance || $sessionStorage.point != data.data.point) {
                        SessionStorageService.setAllSessionStorage(data.data, function (res) {
                            $sessionStorage.memberName = $sessionStorage.name;
                            $sessionStorage.cardId = $sessionStorage.id;
                        });

                        vm.balance = $sessionStorage.balance;
                        vm.points = $sessionStorage.point;
                        return
                    }

                    refreshUserInfo()

                });
            }, 2000);

        }

        function recharge() {
            if (!vm.rechargeItem) {
                alert('');
                console.log('');
                return
            }

            var selectedItem = JSON.parse(vm.rechargeItem);
            var data = {
                mobile: vm.mobile,
                memberName: vm.memberName,
                title: selectedItem.title,
                amount: selectedItem.amount * 100,
                id: selectedItem.id,
                curUrl: $location.absUrl()
            };
            if(cardId =="" ||cardId=="null"||!cardId){
                return
            }
            MyService.rechargeByWeChat(cardId, storeId, token, openId, data).then(function (data) {
                console.log(data);
                if (data.code != 200) {
                    alert(data.message);
                    console.log(data.message);
                    return
                }

                var config = {
                    debug: false,
                    appId: data.signData.appId,
                    timestamp: data.signData.timestamp,
                    nonceStr: data.signData.nonceStr,
                    signature: data.signData.signature,
                    jsApiList: ['checkJsApi', 'chooseWXPay']
                };
                wx.config(config);


                wx.ready(function () {
                    wx.chooseWXPay({
                            debug: false,
                            timestamp: data.payData.timeStamp,
                            nonceStr: data.payData.nonceStr,
                            package: data.payData.prepay_id,
                            signType: data.payData.signType,
                            paySign: data.payData.paySign,
                            success: function (res) {

                                $timeout(function () {
                                    refreshUserInfo();
                                }, 1000);

                                UtilService.showToast(vm, "");
                            },
                            fail: function (res) {
                                // UtilService.showToast(vm, "" + JSON.stringify(res));
                                alert('', res);
                            }
                        }
                    )
                })

            }, function (e) {
                //error msg
            });

        }
        if(cardId =="" ||cardId=="null"||!cardId){
            return
        }
        MyService.getRechargeList(cardId, token).then(function (data) {
            if (data.code != 200) {
                console.log("getRechargeList" + data.message);
                return
            }
            lodash.each(data.data, function (each) {
                var temp = [];
                // temp.push("" + each.amount + "");
                if (each.give) {
                    temp.push("" + each.give + "");
                }
                if (each.point) {
                    temp.push("" + each.point + "");
                }
                if (each.experience) {
                    temp.push("" + each.experience + "");
                }
                if (each.ticketNames) {
                    var yoyoyo = each.ticketNames.split(",");
                    lodash.each(yoyoyo, function (eachTicketName) {
                        temp.push("" + eachTicketName);
                    })
                }
                each.otherInfo = temp.join(",");
                // if (!lodash.isEmpty(temp)) {
                //     each.otherInfo = temp;
                // }
            });
            vm.rechargeList = data.data;
        }, function (e) {
            //error msg
        });
    }
})();

(function () {
    'use strict';

    angular
        .module('vk')
        .controller('myPayCodeController', myPayCodeController);

    /** @ngInject */
    myPayCodeController.$inject = ['$interval', '$location', '$sessionStorage', 'MyService', 'JsBarcode', 'SessionStorageService','$scope'];

    function myPayCodeController($interval, $location, $sessionStorage, MyService, JsBarcode, SessionStorageService,$scope) {
        var cardId = $sessionStorage.cardId;
        var token = $sessionStorage.token;
        var storeId = $sessionStorage.storeId;
        var urlParam = $location.search();
        var theme = urlParam.theme || $sessionStorage.theme;
        var vm = this;
        vm.fukuanma="";
        vm.refresh = refresh;
        vm.goBack = goBack;
        if (storeId==4172 || storeId == 5411){
        vm.fukuanma="";
        }
        function goBack() {
            $location.url('/my?storeId=' + storeId+ "&theme=" + theme);
        }

        function refresh() {

            if(cardId =="" ||cardId=="null"||!cardId){
                return
            }
            MyService.getPayCode(cardId, token).then(function (data) {
                if (data.code != 200) {
                    // alert(data.message);
                    return
                }
                vm.qrcode = data.data.code;
                SessionStorageService.setOneSessionStorage("payCode", data.data.code);
                // if(!document.getElementById("barcode2")){
                //     return
                // }
                JsBarcode("#barcode2")
                    .CODE128C(data.data.code, {fontSize: 20, textMargin: 10, fontWeight: 500, textPosition: "top"})
                    .render();
            }, function (e) {
                //error msg
            });
        }

        //
        if(cardId =="" ||cardId=="null"||!cardId){
            return
        }
        refresh();
        var timer = $interval(function () {
                refresh()
            }
            , 180000
        );
        $scope.$on('$destroy', function (evt) {
            $interval.cancel(timer);
        });
    }
})();

(function () {
    'use strict';

    angular
        .module('vk')
        .controller('myFeedbackController', myFeedbackController);

    /** @ngInject */
    myFeedbackController.$inject = ['$location', 'UtilService', 'MyService', '$sessionStorage'];

    function myFeedbackController($location, UtilService, MyService, $sessionStorage) {
        var cardId = $sessionStorage.cardId;
        var token = $sessionStorage.token;
        var storeId = $sessionStorage.storeId;
        var vm = this;
        vm.memoMaxInput = 30;
        vm.memoInputCount = 0;
        vm.onTextChange = onTextChange;
        vm.onSubmit = onSubmit;
        vm.goBack = goBack;
        var urlParam = $location.search();
        var theme = urlParam.theme || $sessionStorage.theme;
        function goBack() {
            $location.url('/my?storeId=' + storeId+ "&theme=" + theme);
        }

        function onTextChange() {
            var count = vm.feedback.split("");
            vm.memoInputCount = count.length;
            if (count.length > vm.memoMaxInput) {
                count = count.slice(0, vm.memoMaxInput);
                vm.memoInputCount = vm.memoMaxInput;
            }
            vm.feedback = count.join("");
        }

        function onSubmit() {
            if (!vm.feedback) {
                alert("");
                return
            }
            if(cardId =="" ||cardId=="null"||!cardId){
                return
            }
            MyService.postMyFeedback(cardId, storeId, token, vm).then(function (data) {
                if (data.code != 200) {
                    alert(data.message);
                    return
                }
                vm.feedback = "";
                vm.memoInputCount = 0;

                UtilService.showToast(vm, "");

            }, function (e) {
                //error msg
            });
        }
    }
})();

(function () {
    'use strict';

    angular
        .module('vk')
        .controller('myExperienceController', myExperienceController);

    /** @ngInject */
    myExperienceController.$inject = ['$location', 'MyService', '$sessionStorage', "lodash"];

    function myExperienceController($location, MyService, $sessionStorage, lodash) {
        var cardId = $sessionStorage.cardId;
        var storeId = $sessionStorage.storeId;
        var token = $sessionStorage.token;
        var vm = this;
        vm.goBack = goBack;
        if(cardId =="" ||cardId=="null"||!cardId){
            return
        }
        MyService.getMyExperienceDetail(cardId, storeId, token).then(function (data) {
            if (data.code != 200) {
                return
            }
            //+,-,~
            lodash.each(data.data, function (each) {
                if (each.experience > 0) {
                    each.experience = "+" + each.experience;
                } else if (each.experience < 0) {
                    each.experience = "-" + Math.abs(each.experience);
                } else {
                    each.experience = 0;
                }
            });
            vm.myExperience = data.data;
        }, function (e) {
            //error msg
        });

        function goBack() {
            $location.url("/memberLevelDetail?storeId=" + storeId);
        }
    }
})();

(function () {
    'use strict';

    angular
        .module('vk')
        .controller('myChangePwdController', myChangePwdController);

    /** @ngInject */
    myChangePwdController.$inject = ['$location', 'MyService', '$sessionStorage', "UtilService", "$timeout"];

    function myChangePwdController($location, MyService, $sessionStorage, UtilService, $timeout) {
        var cardId = $sessionStorage.cardId;
        var token = $sessionStorage.token;
        var storeId = $sessionStorage.storeId;
        var vm = this;
        vm.msg = {
            warning: false,
            message: ''
        };
        vm.submit = submit;
        vm.goBack = goBack;
        vm.onPwdChange = onPwdChange;
        var urlParam = $location.search();
        var theme = urlParam.theme || $sessionStorage.theme;
        function goBack() {
            $location.url("/my?storeId=" + storeId+ "&theme=" + theme);
        }

        function onPwdChange(item) {
            if (isNaN(item)) {
                vm.password = item.substring(0, item.length - 1);
                return
            }
            if (item.length > 6) {
                vm.password = item.substring(0, item.length - 1);
                vm.msg.warning = true;
                vm.msg.message = '6';
                $timeout(function () {
                    vm.msg.warning = false;
                    vm.msg.message = '';
                }, 2000);
                return
            }
            console.log(item)
        }


        function submit() {
            if (!vm.password || !vm.rePassword) {
                vm.msg.warning = true;
                vm.msg.message = '';
                $timeout(function () {
                    vm.msg.warning = false;
                    vm.msg.message = '';
                }, 2000);
                return;
            }
            if (vm.password.length != 6) {
                vm.msg.warning = true;
                vm.msg.message = '6';
                $timeout(function () {
                    vm.msg.warning = false;
                    vm.msg.message = '';
                }, 2000);
                return
            }
            if (vm.password != vm.rePassword) {
                vm.msg.warning = true;
                vm.msg.message = '';
                $timeout(function () {
                    vm.msg.warning = false;
                    vm.msg.message = '';
                }, 2000);
                return;
            }
            vm.mobile = $sessionStorage.mobile;
            if(cardId =="" ||cardId=="null"||!cardId){
                return
            }
            MyService.changeMyPwd(cardId, token, storeId, vm).then(function (data) {
                if (data.code != 200) {
                    alert(data.message);
                    console.log(data.message);
                    return
                }

                UtilService.showToast(vm, "", goBack);

            }, function (e) {
                //error msg
            });
        }
    }
})();

