(function () {
    'use strict';

    angular
        .module('vk')
        .controller('ForgetPwdController', ForgetPwdController);

    /** @ngInject */
    ForgetPwdController.$inject = ['$interval', '$location', '$sessionStorage', 'lodash', 'ForgetPwdService', 'MyService', 'UtilService', '$timeout',"$window","$scope"];

    function ForgetPwdController($interval, $location, $sessionStorage, lodash, ForgetPwdService, MyService, UtilService, $timeout,$window,$scope) {
        var storeId = $sessionStorage.storeId;
        //
        var send = "false";
        var  csessionid;
        var  sig;
        var tokenStr;
        var scene;
        //
        var waitSec = 60;
        var vCodeWait = waitSec;
        var vm = this;
        vm.msg = {
            warning: false,
            message: ''
        };
        vm.vcodeTitle = "";
        vm.getVcode = getVcode;
        vm.submit = submit;
        vm.goBack = goBack;
        vm.onPwdChange = onPwdChange;
        ali()

        function onPwdChange(item) {
            if (isNaN(item)) {
                vm.pwd = item.substring(0, item.length - 1);
                return
            }
            if (item.length > 6) {
                vm.pwd = item.substring(0, item.length - 1);
                vm.msg.warning = true;
                vm.msg.message = '6';
                $timeout(function () {
                    vm.msg.warning = false;
                    vm.msg.message = '';
                }, 2000);
                return
            }
            console.log(item)
        }

        function goBack() {
            $location.url('/loginByVKApi?storeId=' + storeId)
        }

        function countDown() {
            var timePromise = $interval(function () {
                if (vCodeWait <= 0) {
                    $interval.cancel(timePromise);
                    timePromise = undefined;
                    vCodeWait = waitSec;
                    vm.vcodeTitle = "";
                    vm.vcodeCss = "";
                    send = "false";
                } else {
                    vm.vcodeTitle = "(" + vCodeWait + ")";
                    vCodeWait--;
                    vm.vcodeCss = "my_greyout";
                }
            }, 1000, 100);

        }

        function getVcode() {
            if (!vm.mobile || vCodeWait < waitSec) {
                return
            }
            //
            if (send == "false"){
                document.getElementById("star-dialog").style.display="block";;
                return;
            }

            //
            ForgetPwdService.getVcode(storeId, vm,csessionid,sig,tokenStr,scene).then(function (data) {
                if (data.code != 200) {
                    console.log(data.message);
                    vm.msg.warning = true;
                    vm.msg.message = data.message;

                    $timeout(function () {
                        vm.msg.warning = false;
                        vm.msg.message = '';
                    }, 2000);
                    $timeout(function () {


                    }, 3000);

                    return;
                }
                ali()
                countDown();
                console.log(data.message)
            }, function (e) {
                //error msg
            });


        }

        function submit() {

            if (!vm.mobile || !vm.vcode || !vm.pwd || !vm.rePwd) {
                console.log("");
                vm.msg.warning = true;
                vm.msg.message = '';
                $timeout(function () {
                    vm.msg.warning = false;
                    vm.msg.message = '';
                }, 2000);
                return;
            }
            if (vm.pwd.length != 6) {
                vm.msg.warning = true;
                vm.msg.message = '6';
                $timeout(function () {
                    vm.msg.warning = false;
                    vm.msg.message = '';
                }, 2000);
                return
            }
            if (vm.pwd != vm.rePwd) {
                vm.msg.warning = true;
                vm.msg.message = '';
                $timeout(function () {
                    vm.msg.warning = false;
                    vm.msg.message = '';
                }, 2000);
                return;
            }
            ForgetPwdService.getMyCards(storeId, vm.mobile).then(function (data) {
                if (data.code != 200) {
                    console.log(data.message);
                    vm.msg.warning = true;
                    vm.msg.message = data.message;
                    $timeout(function () {
                        vm.msg.warning = false;
                        vm.msg.message = '';
                    }, 2000);
                    return;
                }
                if (lodash.isEmpty(data.data)) {
                    vm.msg.warning = true;
                    vm.msg.message = "";
                    $timeout(function () {
                        vm.msg.warning = false;
                        vm.msg.message = '';
                    }, 2000);
                }
                vm.cardId = data.data[data.data.length - 1].id;
                ForgetPwdService.resetPwd(storeId, vm).then(function (data) {
                    if (data.code != 200) {
                        vm.msg.warning = true;
                        vm.msg.message = data.message;
                        $timeout(function () {
                            vm.msg.warning = false;
                            vm.msg.message = data.message;
                        }, 2000);
                        return;
                    }

                    UtilService.showToast(vm, "", goBack);

                }, function (e) {
                    //error msg
                });
            }, function (e) {
                //error msg
            });
        }
//
        function ali() {
            var nc = new noCaptcha();
            var nc_appkey = 'FFFF00000000017A69F4';  // ,
            var nc_scene = 'register';  //,
            var nc_token = [nc_appkey, (new Date()).getTime(), Math.random()].join(':');
            var nc_option = {
                renderTo: '#dom_id',//DOM IDDiv
                appkey: nc_appkey,
                scene: nc_scene,
                token: nc_token,
                //   trans: '{"name1":"code100"}',//nc_appkeycode0:;code100:;code200:;code300:
                callback: function (data) {// 
                    // console.log(data.csessionid);
                    // console.log(data.sig);
                    // console.log(nc_token);
                    $timeout(function () {
                        document.getElementById("star-dialog").style.display="none";;
                        getVcode()
                    }, 1000);


                    console.log(vm.showdom);
                    document.getElementById('csessionid').value = data.csessionid;
                    document.getElementById('sig').value = data.sig;
                    document.getElementById('token').value = nc_token;
                    document.getElementById('scene').value = nc_scene;
                    send = "true";
                    csessionid = data.csessionid;
                    sig =  data.sig;
                    tokenStr = nc_token;;
                    scene = nc_scene;;

                }
            };
            nc.init(nc_option);
        }

        $scope.reloadRoute = function () {
            $window.location.reload();

        };
        // function goRegister() {
        //     $location.url('/register');
        // }
    }
})();

